@use '../../styles/colors' as *;
@use '../../styles/breakpoints' as *;

.overlay {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 2;
}

.container {
  max-width: 508px;
  width: 100%;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 18px 0px black;
  border-radius: 8px;
}

.innerContainer {
  padding: 24px;
  display: flex;
  flex-direction: column;
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}

.modalContent {
  min-height: 200px;
  display: flex;
  flex-direction: column;
}

.dateSelection {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.dateFrame {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  padding: 0;
}

.flexRow {
  display: flex;
}

.inputBox {
  width: 40%;
}

.date {
  display: flex;
  flex-direction: column;
  width: 48%;
}

.title {
  font-family: 'Lausanne';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  margin-block-start: 0;
  margin-block-end: 0;
}

.text {
  font-family: 'Lausanne';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.66);
}

.description {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.calendarElement {
  position: fixed;
}
.changeRange {
  margin-top: 1rem;
}
.bold {
  margin-left: 0.5rem;
  font-weight: 600;
}

.buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.close:hover {
  cursor: pointer;
}

.download {
  background-color: $blue-700;
  color: $white !important;
}
.download:hover {
  background-color: $blue-600 !important;
  cursor: pointer;
}
.download:hover > a {
  background-color: $blue-600 !important;
  cursor: pointer;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
@media screen and (max-width: $md) {
  .container {
    top: 0;
    left: 0;
    transform: none;
    width: 100%;
    height: 40vh;
  }
  .buttons {
    margin-top: 1rem;
    justify-content: flex-start;
  }
}
